<template>
  <div class="father animate__animated animate__fadeIn">
    <naver class="dd"><p slot="center">分类</p></naver>
     <scroll ref="scroll" class="scroll-left"><fenleiright :leftt="fenleizong" @zhing="bbtn">
      </fenleiright></scroll>
   <scroll ref="scroll" class="scroll-right"><fenleileft :leftshuju="kaishi" @fashe="loadd"></fenleileft></scroll>
  </div>
</template>
<script>
import { catry, catrytwo } from "../../network/cate.js";
import fenleileft from "./fenleizi/fenleileft.vue";
import fenleiright from "./fenleizi/fenleiright.vue";
import Scroll from "../../components/content/Scroll.vue"
import Naver from "../../components/common/naver/Naver.vue"
import {debounce} from "../../components/common/utils.js"
export default {
  name: "Fenlei",
  data() {
    return {
      fenleizong: [],
      kaishi: [],
      timer:null
    };
  },
  components: {
    fenleileft,
    fenleiright,
    Naver,
    Scroll,
  },
  mounted(){
this.timer=debounce(this.$refs.scroll.refresh,500)
  },
  methods: {
    bbtn(index) {
      
      catrytwo(this.fenleizong[index].maitKey).then((res) => {
        this.kaishi = res.data.data.list;
        this.$refs.scroll.scroll.scrollTo(0,0)
      });
    },
     
    loadd(){
      this.timer()
      //this.$refs.scroll.refresh();
    },
  },
  created() {
    catry().then((res) => {
      this.fenleizong = res.data.data.category.list;
     // console.log(this.fenleizong);
      //console.log(res.data.data.category.list)
    });
    catrytwo("3627").then((res) => {
      this.kaishi = res.data.data.list;
      //console.log(this.kaishi);
    });
  },
};
</script>
<style scoped>
.father{
  height: 100vh;
  width: 100%;
}
.dd{
  background-color:red;
  color:white;
  font-size:20px;
}
.scroll-right{
  position:fixed;
  top:44px;
  left:25%;
  height:calc(100% - 96px);
}
.scroll-left{
  height:calc(100% - 96px);
   position:fixed;
  top:44px;
  left:0;
}
</style>