<template>
<div class="fathe">
   <div v-for="(item,index) in leftt" :key="index"
    @click="aaa(index)" 
    :class="{reds:curr==index}">
        {{item.title}}
   </div>
  
    </div>
</template>
<script>

export default {
  name: 'fenleiright',
  data(){
      return{
curr:0
      }
  },
 
  
  props:{
leftt:{
    type:Array,
    default:[]
}
  },
 
  methods:{
      aaa(index){
         this.curr=index;
         this.$emit('zhing',index)
      }
  },
  

}

</script>
<style scoped>
.fathe{
 display:inline-block;
 height: 800px;
}
.fathe div{
   height: 50px;
 padding-left:10px;
   line-height: 50px;
}
.reds{
    color:red;
}
</style>
